.home-movie-slide-container {
  width: 30vw;
  height: 20vw;
  display: flex;
  padding: 0 55px;
  &:hover .item {
    transform: translateX(-25%);
  }
}
.home-movie-slide {
  // background: green;
  background-size: cover;
  flex: 0 0 19.7%;
  text-align: center;
  margin: 0 2px;
  transition: transform 300ms ease 100ms;
  &:hover ~ .home-movie-slide {
    transform: translateX(25%);
  }
  &:hover {
    transform: scale(1.5) !important;
  }
}
