.movies-main-section {
  display: flex;
  flex-direction: row;
  // margin: 0 auto;
  width: 95%;
  justify-content: space-between;
}
.movies-side-nav {
  // border: 1px solid white;
  width: 15%;
}

.movie-list {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 80%;

  // margin-top: 16vw;
  .movie-category {
    // display: flex;
    // flex-direction: column;
    // margin: 20px 0 ;
    width: 100%;
    max-width: 1400px;
    font-family: 'Fira Sans Condensed', sans-serif;
    height: 28vw;
    max-height: 500px;
    display: flex;
    flex-direction: column;
    // border: 1px solid white;
    .category-title {
      display: grid;
      grid-template-columns: 150px 1fr 100px;
      .category-name {
        color: white;
      }
      .all-btn {
        color: white;
        grid-column-start: 3;
        grid-column-end: 4;
        justify-self: end;
        cursor: pointer;
      }
    }
    .category-movies {
      // align-self: center;
      width: 95vw;
      display: flex;
      height: 100%;
      // border: 1px solid white;
      justify-content: space-between;
      .movie-container {
        width: calc(100% / 7);
        height: 100%;
        // border: 1px solid white;
        border: solid 1px rgb(39, 39, 39);
        background-color: rgb(34, 34, 34);
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
        -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
        -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
        -o-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
        border-radius: 5px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        transition: 0.3s;

        &:hover {
          transform: scale(1.4);
          transition-delay: 0.5s;
        }
        &:hover .title {
          transform: scale(1);
          transition-delay: 0.5s;
        }
        &:hover .btns-container {
          transform: scale(1);
          transition-delay: 0.5s;
        }

        .movie-preview {
          // display: flex;
          // flex-direction: column;
          // justify-items: center;
        }
      }
    }
  }
}

@media only screen and (min-width: 900px) {
  .category-movies {
    align-self: center;
    width: 95vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(6, 13%);
    grid-template-rows: 21vw;
  }
}

.genres-movie-img-container {
  background-size: 100% 100%;
  background-repeat: no-repeat;
  text-align: center;
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: 5px 5px 0 0;
}
.info-continer-movie-card {
  background-image: url(../../styles/img/black-back3.jpg);
  background-size: cover;
  // background-clip: text;
  // -webkit-background-clip: text;
  // color: transparent;
  // border: 1px solid rgb(182, 182, 182);
  border-radius: 0 0 5px 5px;
  padding: 5px;
  > .title {
    padding: 0 10px;
    font-size: 16px;
    // margin: 5px;
    margin-bottom: 2px;
    align-self: center;
    text-align: center;
    color: rgb(255, 255, 255);
  }
}
.genres-container {
  display: flex;
  flex-direction: row;
  text-align: center;
  align-self: center;
  justify-content: center;
  margin-bottom: 5px;
  > .genre {
    color: rgb(236, 236, 236);
    background-color: rgb(68, 68, 68);
    // border: 1px solid rgb(182, 182, 182);
    margin: 2px;
    border-radius: 2px;
    padding: 1px 3px;
    font-size: 9px;
  }
}
.rate-container {
  font-size: 10px;
  display: flex;
  justify-content: space-between;
  text-align: center;
  color: rgb(194, 194, 194);
  > .faRegEye {
    align-self: center;
    color: rgb(233, 233, 233);
  }
  > div {
    > .fa-eye {
      margin-right: 3px;
      font-size: 8px;
    }
  }
}
.btns-container {
  // align-items: center;
  display: flex;
  // justify-content: space-evenly;
  justify-content: center;
  text-align: center;
  background: -webkit-linear-gradient(
    111deg,
    rgb(230, 47, 108),
    rgb(243, 61, 29)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-color: gray;
  font-size: 15px;
  > span {
    margin: 5px;
    &:hover .btns-container {
      background: -webkit-linear-gradient(
        111deg,
        rgb(141, 0, 47),
        rgb(122, 19, 1)
      );
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
}

